@import "./../../global.scss";

.favouritesHelpText {
  width: 100%;
  height: 100%;
  padding: 0 10% 0 10%;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
}

.favourites {
  width: 100%;
  height: 100%;
  padding: 60px 30px 20px 30px;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  overflow: hidden;

  @for $i from 1 through 20 {
    .favouriteOption:nth-child(#{$i}n) {
      animation-delay: #{$i * 0.1}s;
    }
  }

  .favouriteOption {
    flex: 1;
    width: 100%;
    border-bottom: 1px solid var(--text-color-darkest);

    &:first-child {
      border-top: 1px solid var(--text-color-darkest);
    }

    cursor: pointer;
    max-height: 60px;
    display: flex;
    flex-direction: row;

    animation: 500ms ease fade-in-cities both;
    animation-iteration-count: 1;

    @keyframes fade-in-cities {
      0% {
        opacity: 0;
      }

      100% {
        opacity: 1;
      }
    }

    @include for-desktop {
      &:hover {
        .cityDetailsContainer {
          .cityDetails {
            padding-left: 10px;
          }
        }
      }
    }

    .flagContainer {
      flex: 1;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .cityDetailsContainer {
      flex: 4;
      height: 100%;
      display: flex;
      flex-direction: row;
      text-align: left;
      align-items: center;

      .cityDetails {
        font-size: medium;
        font-weight: 500;
        margin: 0;
        transition: all 500ms ease-out 0ms;

        .cityDetailsCountry {
          color: var(--text-color-darker);
        }
      }
    }

    .isInFavouritesIcon {
      flex: 1;

      display: flex;
      align-items: center;
      justify-content: center;

      font-size: 30px;

      .favouriteIcon {
        height: 55%;
        stroke: var(--text-color);

        &.full {
          fill: var(--text-color);
        }
      }
    }
  }
}