@import "./../../global.scss";

.sections {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  touch-action: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  @include for-desktop {
    scroll-behavior: auto;
    scroll-snap-type: y mandatory;
    scroll-snap-stop: always;
  }

  @include for-mobile {
    overflow: hidden;
  }

  @include for-mobile {
    * {
      pointer-events: none;
    }
  }

  .container {
    width: calc(100% - 20px);
    height: 100%;
    margin-left: 10px;
    position: absolute;
    padding-top: 60px;
    touch-action: none;
    transition: all 400ms cubic-bezier(.86, 0, .07, 1) 0ms;
    -webkit-transition: all 400ms cubic-bezier(.86, 0, .07, 1) 0ms;
    -moz-transition: all 400ms cubic-bezier(.86, 0, .07, 1) 0ms;
    -o-transition: all 400ms cubic-bezier(.86, 0, .07, 1) 0ms;

    @include for-desktop {
      scroll-snap-align: start;
    }
  }
}